<template>
  <div class="points_container">
      <head-top head-title="我的积分" goBack="true"></head-top>
      <section class="points_body">
          <section class="points_border">
             <section class="points_to">
                  <div class="jifen">
                  <span class="tx">
                      当前积分
                  </span>
                    <p class="tx_1">
                        <span>0</span>
                        <span>分</span>
                    </p>
                </div>
                <div class="jifen_shuoming">
                        <img src="../../images/description.png" height="24" width="24">
                        <router-link to="/points/detail" class="content_title_style">积分说明</router-link>
                </div>
             </section>
            <div class="botton_type" @click="showdan">
                积分兑换商品
            </div>
          </section>
      </section>
      <div class="footer">
        <header>
            最近30天没有积分数据
        </header>
        <div class="footer_img">
            <img src="../../images/no-log.png" alt="">
            <p>最近30天没有积分数据</p>
            <p>快去赚取大量积分吧</p>
        </div>
      </div>
      <transition name="details_view" mode="out-in">
          <router-view></router-view>
      </transition>
      <alert-tip v-show="showAlert" @closeTip="showAlert=false" :alertText="alertText"></alert-tip>
  </div>
</template>

<script>
import headTop from '../../components/header/header'
import alertTip from '../../components/common/alertTip'
export default {
     components: {
         headTop,
         alertTip
     },data () {
         return {
             showAlert:false,
             alertText:null,
         }
     },methods: {
         showdan()
         {
             this.showAlert=true,
             this.alertText="快去赚取大量积分吧"
         }
     }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
.points_container{
    padding-top:1.95rem;
}
.points_body{
    border:.25rem solid $blue;
    @include wh(100%,7.3rem);
    .points_border{
        border:0.025rem solid $blue;
        height: 6.74rem;
        border-radius: 0.2rem;
        box-shadow: 0 0 0 .3rem $blue;
        .points_to
        {
            display: flex;
            padding:0 0.2rem;
            justify-content: space-between;
            .jifen{
                .tx{
                    @include sc(0.6rem,#333);
                }
                .tx_1{
                    span:nth-of-type(1)
                    {
                        @include sc(1.5rem,#333);
                    }
                    span:nth-of-type(2)
                    {
                        @include sc(0.5rem,#333);
                    }
                }
            }
            .jifen_shuoming{
                align-items: center;
                img{
                    @include wh(.6rem,.6rem);
                }
                .content_title_style{
                    @include sc(0.5rem,$blue);
                }
            }
        }
        
    }
}
.botton_type{
    background: #f60;
    margin:0 0.4rem;
    margin-top: 1rem;
    @include sc(0.7rem,#fff);
    padding:0.6rem;
    border-radius: 0.2rem;
    text-align: center;
}
.footer{
    padding:0.4rem;
    header{
        margin-top: .3rem;
        @include sc(0.6rem,#ddd);
    }
    .footer_img{
        margin-top: 1.4rem;
        img{
           
        }
        text-align: center;
        p{
            @include sc(0.6rem,#333);
            line-height: 1rem;
        }
    }
}
.details_view-enter-active,.details_view-leave-active{
    transition: all .4s;
}
.details_view-enter,.details_view-leave-active{
    transform: translate3d(2rem,0,0);
    opacity: 0;
}
</style>



